.menu {
  top: 0;
  position: fixed;
  opacity: 0;
  z-index: 7;
  width: 100%;
  height: 100%;
  background-color: #101011;
  padding-top: 12vw;
  transform: translateX(100%);
  transition: transform 600ms linear, opacity 300ms linear;
}
.show-menu {
  transform: translatex(0);
  opacity: 1;
}

.container {
  width: 100%;
  padding-left: 5vw;
  margin-right: auto;
  margin-left: auto;
}
.menu.show-menu .row {
  transform: translateY(0);
}
.menu ul {
  padding-left: 0;
  list-style: none;
  margin: 0;
  color: white;
}
.menu ul li {
  position: relative;
  /* background-color: blue; */
  padding-top: 8px;
  padding-bottom: 8px;
  margin-bottom: 2.4vw;
}
.menu ul li {
  list-style: none;
  background-image: none;
  margin: 0;
  /* padding: 0; */
  line-height: normal;
}
.menu ul li small {
  display: block;
  font-family: Montserrat;
  font-weight: 900;
  font-size: 10px;
  text-transform: uppercase;
  margin-bottom: 11px;
  outline: 0;
}
.menu ul li a {
  font-size: calc(18px + 2.1vw);
  line-height: calc(18px + 1.8vw);
  text-transform: uppercase;
  font-weight: 900;
  text-decoration: none;
  font-family: Montserrat;
}
.menu ul li a {
  transition: color 300ms linear;
}

.menu li:hover > span {
  opacity: 1;
}
.menu ul li a:after {
  content: "";
  width: 32px;
  height: 32px;
  border-radius: 100%;
  border: 4px solid #003cff;
  display: inline-block;
  position: absolute;
  margin-left: -8px;
  z-index: -1;
  margin-top: -9px;
  transform: scale(0);
  transition: transform 300ms linear;
}
.menu ul li span {
  font-weight: bold;
  text-transform: uppercase;
  position: absolute;
  bottom: 0;
  -webkit-text-stroke: 1px rgba(99, 99, 99, 0.45);
  -webkit-text-fill-color: rgba(255, 255, 255, 0);
  text-shadow: none;
  font-size: 18.4vw;
  line-height: 12vw;
  font-family: mont !important;
  font-weight: bold !important;
  font-size: 5.7vw;
  line-height: 3.4vw;
  z-index: -1;
  left: 5%;
  transition: opacity 200ms linear;
  opacity: 0;
  pointer-events: none;
}

@media (min-width: 1200px) {
}
.container {
  max-width: 1200px;
}

@media (min-width: 992px) {
}
.container {
  max-width: 960px;
}

@media (min-width: 768px) {
}
.container {
  max-width: 720px;
}

@media (min-width: 576px) {
}
.container {
  max-width: 5400px;
}
